










import { Component, Vue } from "vue-property-decorator";
import { Subject, from, of } from "rxjs";
import { ajax } from "rxjs/ajax";
import { map, mergeMap, reduce, catchError, tap, toArray, filter, pluck, distinctUntilChanged, debounceTime, take } from "rxjs/operators";

@Component
export default class Watchlist extends Vue {
  stockdb: any
  symbols: any
  masterList: any
  notice!: string

  searchSymbol$ = new Subject()
  addFavorite$ = new Subject()

  data() {
    return {
      notice: '',
      masterList: [],
      symbols: [],
      gainers: [],
      losers: []
    }
  }

  created() {
    this.stockdb.getWatchList()
      .subscribe(
        (result: any) => {
          console.log(result)
        },
        (error: Error) => {
          if (error.message === 'missing') {
            this.notice = 'Nothing in watchlist.. let\'s add one from '
          }
        }
      )
  }
}
